@import url('https://fonts.googleapis.com/css2?family=League+Spartan:wght@300&family=Poppins&display=swap');




ul {
    list-style: none;
    margin: 0;
    padding: 0;
}


.overlay {
    cursor: pointer;
    position: relative;
    top: -13vh;
    height: 15vh;
    width: 100%;
    background-color: #3A41DD;
    line-height: 2rem;
    padding: 1rem;
    opacity: 0;
    font-size: 2vh;
    text-align: center;
    align-items: center;
    transition: 0.3s ease-in-out;
}

.overlay:hover {
    top: -15vh;
    opacity: 0.9;
    transition: 0.3s ease-in-out;
}

.wrapper {
    position: relative;
    height: fit-content;
    /* background-color: #ce1616; */
}


.navbar {
    position: absolute;
    height: 100%;
    width: 8vw;
    background-color: #3A41DD;
    color: white;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    align-items: center;
    font-size: 4vh;
}

.navbar_links {
    position: relative;
    display: flexbox;
    width: 100%;
    top: -13vh;
}

.div_style, .div_style2  {
    opacity: 0.2;
    text-align: center;
}

.div_style {
    position: relative;
    top: 2vh;
}

.div_style2 {
    position: relative;
    top: -12vh;
}

.navbar_logo {
    position: relative;
    top: 2vh;
    width: 100%;
}

.navbar_logo img {
    width: 100%;
    position: relative;

    /* cursor: pointer; */
}

.navbar_item {
    text-align: center;
    justify-content: center;
    position: relative;
    top: 0vh;
    font-size: 4vh;
    padding: 0vh;
    transition: 0.1s ease-in-out;
    height: 8vh;
    line-height: 7vh;
    color: white;
    /* transform: translate(-50%, -50%); */
}

.navbar_item:hover {
    background-color: #4c54e9;
    cursor: pointer;
    font-size: 4.5vh;
    transition: 0.1s ease-in-out;

}

/* .navbar_links {
    position: relative;
    top: -13vh;
} */


.navbar_item a {
    color: white;
}


.containPage {
    position: absolute;
    left: 8vw;
    z-index: 50;
    transition: 0.2s ease-in-out;

}

.containPage3 {
    position: absolute;
    left: 8vw;
    z-index: 50;
    transition: 0.2s ease-in-out;
    padding: 0;
    margin: 0;


}


.Card1 {
    position: absolute;
    left: 4vw;
    margin: 0 ;
    border-radius: 50px;
    top: 10vh;
    height: 78vh;
    width: 40vw;
    background-color: #ffffff;
}

.Card2 {
    position: absolute;
    left: 48vw;
    margin: 0 ;
    height: 78vh;
    width: 40vw;
    border-radius: 50px;
    top: 6vh;
    height: fit-content;
    width: fit-content;
    background-color: #ffffff;
}

.box1 {
    position: relative;
    height: 70vh;
    width: 40vw;
    left: 0;
    color: #676767;
    font-size: 3vh;
    padding: 4vw;
}

.box1 > p {
    text-align: center;
}

.box2 {
    position: relative;
    height: 85vh;
    width: 40vw;
    left: 0;
    color: #676767;
    font-size: 7vh;
    padding: 2vw;
}

.box2 > p {
    font-size: 2.5vh;
    padding-top: 0;
    position: absolute;
    bottom: 8vh;
}

.ContCard {
    position:static;
    height: 60vh;
    width: 20vw;
    text-align: center;
    background-color: white;
    z-index: 0;
    border-radius: 2vh;
    transition: 0.4s ease-in-out;
    color: #2a2a2a;
    text-decoration: none;
    

}

.ContCard > a {
    color: #2a2a2a;
    text-decoration: none;
}

.ContCard:hover .iconN  {
    cursor: pointer;
    font-size: 18vh;
    transform-origin: center center;
    color: #4c54e9 ;
    transform: scale(1.1) ;
    transition: 0.2s ease-in-out;

}

.ContCard h1 {
    line-height: 20vh;
}

.containPage2 {
    position: absolute;
    transition: 0.2s ease-in-out;
}

.wrapper2 {
    position: absolute;
    left: 8vw;
    height: 100vh;
    width: 92vw;  
    display: grid;
    grid-template-columns: repeat(4, auto);
    justify-items: center;
    align-items: center;    

}


.iconN {
    font-size: 18vh;
    text-decoration: none;
}

.iconN:visited {
    font-size: 18vh;
    text-decoration: none;
}


