.codingImage {
    z-index: 800;
    width: 35vw;

    position: absolute;
    top: 0;
}

.front {
    position: relative;
    display: grid;
    grid-template-columns: repeat(4, auto);
    gap: 4vw;
    /* border: #3A41DD solid; */
    font-size: 8vh;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.box3 {
    font-size: 2vh;
    text-align: center;
    color: #2a2a2a;
    font-weight: 800;
}

.back > img {
    height: 7vh;
}

.back {
    position: relative;
    display: grid;
    grid-template-columns: repeat(4, auto);
    gap: 4vw;
    /* border: #3A41DD solid; */
    font-size: 8vh;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.cards {
    position: absolute;
    height: 84vh ;
    width: 84vw;
    background-color: #ffffff;  
    margin: 4vw;
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: repeat(2, auto);
    justify-items: center;
    align-items: center;
    /* border-radius: 2rem; */

}

.projCard {
    position: relative;
    height:fit-content ;
    /* width: fit-content; */
    margin: 4vh;
    /* background-color: #676767; */
    transition: 0.2s ease-in-out;
    box-shadow: 1px 17px 14px -10px rgba(0,0,0,0.17);
    -webkit-box-shadow: 1px 17px 14px -10px rgba(0,0,0,0.17);
    -moz-box-shadow: 1px 17px 14px -10px rgba(0,0,0,0.17);
}

.projCard:hover {
    /* opacity: 0.2; */
    background-color: #000000;
    transition: 0.2s ease-in-out;
}

.projCard p {
    position: absolute;
    height: auto;
    top: 8vh;
    width: 30vw;
    color: #1c1c1c;
    font-size: 2vh;

    margin: 0;
    padding: 2vh;
    opacity: 0;
    text-align: center;
    align-items: center;
    color: #ffffff;
    transition: 0.2s ease-in-out;
    /* background-color: #3A41DD; */
}

.projCard img {
    height: auto;
    width: 30vw;
    z-index: 50;
    padding: 0;
    margin: 0;
    transition: 0.2s ease-in-out;
}

.projCard h5 {
    /* position: relative; */
    color: #2a2a2a;
    font-size: 3vh;
    transition: 0.2s ease-in-out;
    z-index: 500;
    margin: 0;
    padding: 0;
    height: auto;
    width: 30vw;
    /* background-color: #3A41DD; */
    text-align: center;
    transition: 0.2s ease-in-out;
}

.projCard:hover img {
    opacity: 0.2;
    transition: 0.2s ease-in-out;
}


.projCard:hover p {
    opacity: 1;
    /* transform: scale(1.05); */
    transition: 0.2s ease-in-out;
}

.projCard:hover h5 {
    transition: 0.2s ease-in-out;
    color: #ffffff;
    font-size: 4vh;
    visibility: visible;
}

.overlayImage {
    position: absolute;
    height: auto;
    visibility: hidden;
    z-index: -40;
    box-shadow: 1px 17px 14px -10px rgba(0,0,0,0.17);
    -webkit-box-shadow: 1px 17px 14px -10px rgba(0,0,0,0.17);
    -moz-box-shadow: 1px 17px 14px -10px rgba(0,0,0,0.17);
}

.projCard:hover .overlayImage {
    visibility:visible;
    transition: 0.2s ease-in-out;
    color: #ffffff;
    font-size: 3vh;
}

.projCard:hover .btn {
    visibility: visible;
    transition: 0.2s ease-in-out;
}

.btn {
    position: absolute;
    bottom: 2vh;
    left: 50%;
    visibility: hidden;
    transform:translate( -50%, -50%);
    align-items: center;
    justify-content: center;
    transition: 0.1s ease-in-out;
}

.clickable {
    display: none;
}

@media (max-width: 480px) {
    .navbar {
        position: fixed;
        height: 12vh;
        width: 100%;
        background-color: #3A41DD;
        padding: 0;
        color: white;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        align-items: center;
        font-size: 4vh;
        z-index: 10000000000000000000;
    }

    .div_style, .div_style2  {
        display: none;
    }

    .navbar_logo {
        display: none;
    }

    .wrapper {
        position: absolute;
        width: 100%;
        top: 0vh;
        left: 0;
        padding: 0;
        margin: 0;

    }

    .navbar_links {
        position: relative;
        height: 12vh;
        padding: 0;
        margin: 0;
        top: 0vh;
        display: grid;
        grid-template-columns: repeat(3, auto);
    }


    .navbar_logo img {
        display: none;
    }

    .navbar_item {
        position: relative;
        top: 0vh;
        height: 100%;
        padding: 2vh;
        margin: 0;
        font-size: 6vh;

    }

    .clickable {
        display: block;
        position: absolute;
        height: 31vh;
        width: 100%;
        z-index: 80000000;
    }

    .projCard p {
        display: none;
    }


    .Card1 {
        position: absolute;
        margin: 0vh ;
        left: 2vw;
        top: 15vh;
        height: 84vh;
        width: 96vw;
        /* background-color: #cb4040; */
    }

    .box1 {
        /* background-color: transparent; */
        height: 100%;
        padding: 4vh;
        width: 100%;
        font-size: 3vh;
    }
    
    .Card2 {
        position: absolute;
        border-radius: 50px;
        left: 4vw;
        top: 102vh;
        background-color: #ffffff;
        z-index: 1000;
        height: 72vh;
        margin-bottom: 10vh;
        width: 92%;
    }

    .box2 {
        height: 100%;
        width: 100%;
        margin-bottom: 5vh;
    }

    .box2 p {
        top: 0vh;
        font-size: 3vh;
        padding: 5vh;
    }

    
    .containPage {
        position: relative;
        height: 100%;
        width: 100%;
        z-index: -800000;
        left: 0;
        transition: 0.2s ease-in-out;
    }

    .front {
        position: relative;
        display: grid;
        grid-template-columns: repeat(4, auto);
        gap: 4vw;
        /* border: #3A41DD solid; */
        font-size: 8vh;
        text-align: center;
        align-items: center;
        justify-content: center;
    }

    .codingImage {
        overflow: hidden;
        visibility: hidden;
        position: fixed;
    }

    .containPage3 {
        position: relative;
        top: 12vh;
        height: 100%;
        width: 100vw;
        left: 0;
        transition: 0.2s ease-in-out;
    }

    .cards {
        position: relative;
        left: 0;
        top: 1vh;
        padding: 0vh;
        margin: 0vh 5%;
        height: 170vh;
        width: 90vw;
        display: grid;
        grid-template-columns: repeat(1, auto);
    }

    .projCard img {
        height: auto;
        width: 70vw;
        z-index: 0;
        padding: 0;
        margin: 0;
        transition: 0.2s ease-in-out;
    }

    .projCard h5 {
        width: 100%;
        font-size: 3vh;
    }

    .projCard:hover h5 {
        width: 100%;
        font-size: 3vh;
    }


    .wrapper2 {
        left: 0;
        top: 10vh;
        height: 140vh;
        width: 100vw;
        height: fit-content;
        display: grid;
        grid-template-columns: repeat(1, auto);
        grid-template-rows: repeat(4, auto);
        grid-gap: 3vh;
    }

    .btn {
        display: none;
    }

    .containPage2 {
        height: 100%;
        width: 100%;
    }

    .ContCard {
        width: 50vw;
        height: 30vh;
        margin: 4vh;
    }

    .ContCard h1 {
        padding: 0;
        margin: 0;
        font-size: 2vh;
        display: none;
    }

    .overlayImage {
        position: absolute;
        height: auto;
        visibility: hidden;
        z-index: -40;
        box-shadow: 1px 17px 14px -10px rgba(0,0,0,0.17);
        -webkit-box-shadow: 1px 17px 14px -10px rgba(0,0,0,0.17);
        -moz-box-shadow: 1px 17px 14px -10px rgba(0,0,0,0.17);
    }
}

